.form-control, .form-contact input:not(:disabled):not([type="submit"]) { display: block; width: 100%; padding: 5px 0; font-size: 14px; line-height: 1.5; background-color: transparent; background-image: none; background-clip: padding-box; border-radius: 0; transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s; }
